import axios from 'axios'
import React from 'react'
import { useState } from 'react'
import { useEffect } from 'react'
import FormDropdown from './FormDropdown'
import FormInput from './FormInput'
import FormToggle from './FormToggle'
import FormUpload from './FormUpload'

function ComputerDetails({setSelectedFile}) {
    const [ laptopBrandId, setLaptopBrandID ] = useState([])
    const [ laptopCPU, setLaptopCPU ] = useState([])
    useEffect(()=>{
        axios.get('/brands').then(response => {
            setLaptopBrandID(response.data.data)
        }).catch(error => console.log(error))
        
        axios.get('/cpus').then(response => {
            setLaptopCPU(response.data.data)
        }).catch(error => console.log(error))
    }, [])

    return (
        <div>
            <FormUpload setSelectedFile={setSelectedFile} />
            <div className='grid md:grid-cols-2 md:gap-6 items-center'>
            <FormInput name={'laptop_name'} type={'text'} label={' '} placeholder={'HP'} text={' , , !@#$%^&*()_+= '}/>
            <FormDropdown name={'laptop_brand_id'} label={' '} options={laptopBrandId}/>
            </div>  
            <div className='grid md:grid-cols-3 md:gap-6 items-center border-t border-gray-300 mt-8'>
            <FormDropdown name={'laptop_cpu'} label={'CPU'} options={laptopCPU} useNameAsValue={true}/>
            <FormInput name={'laptop_cpu_cores'} label={'CPU- '} placeholder={'14'} text={' '}/>
            <FormInput name={'laptop_cpu_threads'} label={'CPU- '} placeholder={'365'} text={' '}/>
            </div>
            <div className='grid md:grid-cols-2 md:gap-6'>
            <FormInput name={'laptop_ram'} label={' RAM (GB)'} placeholder={'0'} text={' '}/>
            <FormToggle name={'laptop_hard_drive_type'} label={' '} values={['HDD', 'SSD']} names={['HDD', 'SSD']}/>
            </div>  
            <div className='grid md:grid-cols-2 md:gap-6 border-t border-gray-300 mt-8'>
            <FormInput name={'laptop_purchase_date'} label={'  ()'} placeholder={'//'} text={''}/>
            <FormInput name={'laptop_price'} label={' '} placeholder={'0000'}  text={' '}/>
            </div> 

            <div>
                <FormToggle name={'laptop_state'} label={' '}  values={['new', 'used']} names={['', '']}/>
            </div> 
        </div>
  )
}

export default ComputerDetails