import axios from 'axios'
import { useField } from 'formik'
import React from 'react'
import { useState } from 'react'
import { useEffect } from 'react'
import FormDropdown from './FormDropdown'
import FormInput from './FormInput'


function EmployeeDetails({values}) {
    const [ teams, setTeams ] = useState([])
    const [ positions, setPositions ] = useState([])
    const [ teamID, setTeamID ] = useState()
    const [ field ] = useField('team_id')
    

    useEffect(() => {
        setPositions(
            positions.filter((position) => {
                return position.team_id == teamID;
            })
        )
        console.log('working')
    },[teamID])

    useEffect(()=>{
        axios.get('/teams').then(response => {
            setTeams(response.data.data)
        }).catch(error => console.log(error))
        axios.get('/positions').then(response => {
            setPositions(response.data.data)
        }).catch(error => console.log(error))
    },[])

    return (
        <div>
            <div className='grid md:grid-cols-2 md:gap-6'>
                <FormInput name={'name'} type={'text'}label={''} placeholder={''} text={' 2 ,  '}/>
                <FormInput name={'surname'} type={'text'} label={''} placeholder={''}  text={' 2 ,  '}/>
            </div>                
            <FormDropdown name={'team_id'} options={teams} label='' setTeamID={setTeamID}/>
            <FormDropdown name={'position_id'} options={positions} label=''/>
            <FormInput name={'email'} type={'email'} label={''} placeholder={''} text={'  @redberry.ge-'}/>
            <FormInput  name={'phone_number'} type={'text'} label={' '} placeholder={'+995'} text={'   - '}/>
        </div>
  )
}

export default EmployeeDetails