import * as Yup from "yup";

export const formValidation = Yup.object().shape({
  name: Yup.string().min(2, ' 2 ,  ').required("  ").matches( (/^[-]+$/), '    '),
  surname: Yup.string().min(2, ' 2 ,  ').required("  ").matches( (/^[-]+$/), '    '),
  team_id: Yup.number().required("  "),
  position_id: Yup.number().required("  "),
  phone_number: Yup.string().matches(/^\+995\d{9}$/, '     +995 XX XX XX').min(13, '   ').max(13, '   ').required("  . "),
  email: Yup.string().email('   ').matches(/@redberry.ge/, '    example@redberry.ge').required('  @redberry.ge-'),
  laptop_name: Yup.string().required("   "),
  // laptop_image: Yup.string().required(),
  laptop_brand_id: Yup.number().required("  "),
  laptop_cpu: Yup.string().required("  "),
  laptop_cpu_cores:  Yup.number().required("   "),
  laptop_cpu_threads:  Yup.number().required("   "),
  laptop_ram:  Yup.number().required("   "),
  laptop_hard_drive_type: Yup.string().required("   "),
  laptop_state: Yup.string().required("  "),
  laptop_purchase_date: Yup.date('  '),
  laptop_price: Yup.number().min(1, '   0 ').required("  "),
  });
